p {
  font-family: 'Montserrat', sans-serif;
  padding: 1rem;
}

h2 {
  padding: 2rem;
  font-size: large;
  font-weight: 700;
}
/* center the blockquote in the page */
.blockquote-wrapper {
  display: flex;
  width: 100vw;
  padding: 0 20px;
  margin-bottom: 50px;
  margin-top: 0;
}
/* Blockquote main style */
.blockquote {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  color: #ffffff;
  padding: 30px 0;
  width: 100%;
  max-width: 500px;
  z-index: 1;
  align-self: center;
  border-top: solid 1px;
  border-bottom: solid 1px;
}

/* Blockquote header */
.blockquote h3 {
  position: relative;
  color: #ffffff;
  font-size: 1.5rem;
  font-weight: 800;
  line-height: 1;
  margin: 0;
}

/* Blockquote right double quotes */
.blockquote:after {
  position: absolute;
  content: '‚Äù';
  color: rgba(0, 255, 0, 1);
  font-size: 10rem;
  line-height: 0;
  bottom: -2rem;
  right: 30px;
}

/* increase header size after 600px */
@media all and (min-width: 600px) {
  .blockquote h3 {
    font-size: 2rem;
  }
}

/* Blockquote subheader */
.blockquote h4 {
  position: relative;
  color: whitesmoke;
  font-size: 1.3rem;
  font-weight: normal;
  line-height: 1;
  margin: 0;
  padding-top: 20px;
  z-index: 1;
}
