.movieShow {
  max-width: 15rem;
  display: inline-block;
  align-self: center;
}

.movieShow h4 {
  text-align: center;
  margin-top: 0;
  width: 100%;
}

.movieShow img {
  max-width: 100%;
  transform: scale(0.9);
  object-fit: cover;
}

#false {
  filter: grayscale(100%);
}

.movieShow > img:hover {
  transform: scale(1);
  transition: all ease 0.5s;
}
